.blog {
  max-width: 75rem;
  margin: 0 auto; }
  .blog__feature {
    padding: 2rem; }
    .blog__feature--container {
      height: 60vh;
      background: #f9f9f9;
      display: flex;
      justify-content: center;
      overflow: hidden; }
    .blog__feature__content {
      flex: 0 0 40%;
      padding: 4rem;
      align-self: center;
      cursor: pointer; }
    .blog__feature__title {
      display: inline-block;
      padding: 1rem 0rem; }
      .blog__feature__title::after {
        content: "";
        display: block;
        width: 0;
        height: 4px;
        border-radius: 2px;
        background: #141414;
        transition: width 0.3s; }
      .blog__feature__title:hover::after {
        width: 100%; }
    .blog__feature__desc {
      color: #3b3b3b;
      font-weight: 400;
      font-size: 1rem;
      padding-bottom: 0.5rem; }
    .blog__feature__image-box {
      width: 100%;
      cursor: pointer; }
      .blog__feature__image-box:hover {
        filter: opacity(0.85); }
    .blog__feature__image {
      height: 100%;
      background-position: 50% 50%;
      background-size: cover; }
  .blog .read__more {
    font-size: 1rem;
    font-weight: 300;
    color: var(--color-grey-dark-1);
    cursor: pointer; }
    .blog .read__more span {
      margin-left: 5px;
      transition: margin 0.3s; }
    .blog .read__more:hover span {
      margin-left: 7px; }

h1,
h2,
h3,
h4,
h5,
h6,
p {
  font-family: "Roboto"; }
